<template>
<NavBar/>
<div class="w-full">
    <aside class="sidebar pt-10 fixed top-0 bottom-0 border-r border-gray-100 lg:left-0 p-2 w-[300px] overflow-y-auto text-center text-cyan-600 select-none">
      
      <div class="items-center text-left duration-300 cursor-pointer">
        <i class="bi bi-house-door-fill"></i>
        <router-link to="/account" class="py-2 px-4 w-100 block font-bold text-lg rounded" 
                      :class="{ 'bg-orange-100': activeLink === 'account' }"
                      @click="setActive('account', '帳務管理')">
          <BanknotesIcon class="mr-2 h-5 w-5 inline" />帳務管理
        </router-link>
      </div>
      
      <div class="items-center text-left duration-300 cursor-pointer">
        <i class="bi bi-bookmark-fill"></i>
        <router-link to="/list" class="py-2 px-4 w-100 block font-bold text-lg rounded"
                      :class="{ 'bg-orange-100': activeLink === 'list' }"
                      @click="setActive('list', '名單管理')">
          <ListBulletIcon class="mr-2 h-5 w-5 inline" />名單管理
        </router-link>
      </div>
      
      <div class="items-center text-left duration-300 cursor-pointer">
        <i class="bi bi-bookmark-fill"></i>
        <router-link to="/auth" class="py-2 px-4 w-100 block font-bold text-lg rounded"
                      :class="{ 'bg-orange-100': activeLink === 'auth' }"
                      @click="setActive('auth', '權限管理')">
          <Cog6ToothIcon class="mr-2 h-5 w-5 inline" />權限管理
        </router-link>
      </div>
      
      <div class="py-1 px-4 mt-2 items-center text-left rounded-md duration-300 cursor-pointer" @click="dropdown()">
        <i class="bi bi-chat-left-text-fill"></i>
        <div class="flex w-full items-center">
          <span class="ml-4 font-bold text-lg">
            <UserGroupIcon class="h-5 w-5 inline" /> 會員管理</span>
            <ChevronUpIcon v-if="!isDropdownOpen" class="ml-4 h-5 w-5 inline" />
            <ChevronDownIcon v-else class="ml-4 h-5 w-5 inline" />
        </div>
      </div>
      <div class="mt-2 mx-auto w-3/5 text-left text-base font-bold text-lg" v-bind:class="{'hidden': isDropdownOpen}">
        <h1 class="cursor-pointer">
          <router-link to="/account" class="p-2 mt-1 w-100 block font-bold text-lg rounded"
                        :class="{ 'bg-orange-100': activeLink === 'member/userList' }"
                        @click="setActive('member/userList', '使用者清單')">
            使用者清單
          </router-link>
        </h1>
        <h1 class="cursor-pointer">
          <router-link to="/list" class="p-2 mt-1 w-100 block font-bold text-lg rounded"
                        :class="{ 'bg-orange-100': activeLink === 'member/userActionHistroy' }"
                        @click="setActive('member/userActionHistroy', '使用者操作紀錄')">
                        使用者操作紀錄
          </router-link>
        </h1>
        <h1 class="cursor-pointer">
          <router-link to="/auth" class="p-2 mt-1 w-100 block font-bold text-lg rounded"
                        :class="{ 'bg-orange-100': activeLink === 'member/userGroupRole' }"
                        @click="setActive('member/userGroupRole', '群組權限管理')">
                        群組權限管理
          </router-link>
        </h1>
      </div>
    </aside>
  </div>
  
  <main class="main-content relative h-full">
    <router-view />
  </main>
</template>

<script>
import NavBar from './components/NavBar.vue'
import { BanknotesIcon, ChevronUpIcon, ChevronDownIcon, Cog6ToothIcon, ListBulletIcon, UserGroupIcon } from '@heroicons/vue/24/solid';

export default {
  name: 'App',
  components: {
    NavBar,
    BanknotesIcon,
    ChevronUpIcon,
    ChevronDownIcon,
    Cog6ToothIcon,
    ListBulletIcon,
    UserGroupIcon
  },
  data() {
    return {
      isDropdownOpen: false,
      activeLink: localStorage.getItem('activeLink') || '',
    }
  },
  methods: {
    dropdown() {
      this.isDropdownOpen = !this.isDropdownOpen;
    },
    setActive(link, routerName) {
      this.activeLink = link;
      localStorage.setItem('activeLink', link);
      localStorage.setItem('routeName', routerName);
    }
  },
  watch: {
    '$route.path'(newPath) {
      const previousLink = localStorage.getItem('activeLink')?.replace(/^\//, '');
      this.activeLink = previousLink ? previousLink : newPath.replace(/^\//, '');
    }
  }
}
</script>

