<template>
  <div class="px-20 py-10 text-left">
    <!-- breadcrumb -->
    <div class="py-2 text-left">
      <span class="text-cyan-600 font-bold">
        <HomeIcon class="h-4 w-4 inline" /> 首頁
      </span>
      <span class="mx-1 text-gray-600">/</span>
      <span class="text-cyan-600 font-bold">帳務管理</span>
    </div>
    
    <h5 class="title my-2 text-2xl font-bold">帳務管理</h5>
    
    <div class="accordion mt-4 border border-gray-200 bg-cyan-50 rounded-md">
      <div class="border-b border-gray-200">
        <button
          @click="toggleAccordion(1)"
          class="py-2 px-6 w-full flex items-center justify-between text-left font-bold text-cyan-600">
          篩選
          <svg
            class="w-4 h-4 transition-transform transform"
            :class="{'rotate-180': activeIndex === 1}"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 20 20"
            fill="currentColor"
            aria-hidden="true"
          >
            <path
              fill-rule="evenodd"
              d="M10 18a1 1 0 01-.707-.293l-7-7a1 1 0 111.414-1.414L10 15.586l6.293-6.293a1 1 0 111.414 1.414l-7 7A1 1 0 0110 18z"
              clip-rule="evenodd"
            />
          </svg>
        </button>
        
      </div>
      <!-- 內容 -->
      <div 
        class="px-6 py-4 bg-white rounded-b"
        v-show="activeIndex === 1"
        :class="{'hidden': activeIndex !== 1}"
      >
        <div>
          <div class="mb-3 md:flex md:items-center">
            <div class="md:w-2/12">
              <label class="pr-4 mb-1 block text-gray-500 font-bold md:text-left md:mb-0">帳號</label>
            </div>
            <div class="md:w-6/12">
              <input v-model="userAccount" class="border border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-cyan-500" 
                      type="text" placeholder="請輸入帳號">
            </div>
          </div>
          
          <div class="mb-3 md:flex md:items-center">
            <div class="md:w-2/12">
              <label class="pr-4 mb-1 block text-gray-500 font-bold md:text-left md:mb-0">姓名</label>
            </div>
            <div class="md:w-6/12">
              <input v-model="username" class="border border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-cyan-500" 
                      type="text"  placeholder="請輸入姓名">
            </div>
          </div>
          
          <div class="mb-3 md:flex md:items-center">
            <div class="md:w-2/12">
              <label class="pr-4 mb-1 block text-gray-500 font-bold md:text-left md:mb-0">群組</label>
            </div>
            <div class="md:w-6/12">
              <select v-model="group" class="border border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-cyan-500 cursor-pointer" >
                <option v-for="groupItem in groupList" :key="groupItem.id" :value="groupItem.value">{{ groupItem.text }}</option>
              </select>
            </div>
          </div>
          
          <div class="mb-3 md:flex md:items-center">
            <div class="md:w-2/12">
              <label class="pr-4 mb-1 block text-gray-500 font-bold md:text-left md:mb-0">狀態</label>
            </div>
            <div class="md:w-6/12 flex space-x-4">
              <div v-for="statusItem in statusList" :key="statusItem.id" :value="statusItem.value"
              class="flex items-center space-x-1">
                <input type="radio" :id="statusItem.id" :value="statusItem.value" v-model="status" name="options" class="focus:ring-cyan-500 h-4 w-4 text-cyan-600 border-gray-300 cursor-pointer">
                <label :for="statusItem.id" class="text-gray-700 cursor-pointer">{{ statusItem.text }}</label>
              </div>
              
            </div>
          </div>

          <!-- button -->
          <div class="mt-6 text-right">
            <button class="bg-cyan-600 hover:bg-cyan-700 text-white font-bold py-2 px-4 rounded-full" @click="applyFilter">
              <MagnifyingGlassIcon class="h-4 w-4 inline" />篩選
            </button>
            <button class="bg-orange-400 hover:bg-orange-500 text-white font-bold py-2 px-4 ml-3 rounded-full">
              <DocumentArrowDownIcon class="h-4 w-4 inline" />匯出
            </button>
          </div>
        
        </div>
      </div>
    </div>
    
    <!-- datatable -->
    <div class="mt-10 py-2 px-4 border rounded-md">
      <table id="accountDatatable" class="display">
        <thead>
          <tr>
            <th>帳號</th>
            <th>姓名</th>
            <th>群組</th>
            <th>狀態</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in items" :key="item.id">
            <td>{{ item.account }}</td>
            <td>{{ item.name }}</td>
            <td>{{ item.group }}</td>
            <td>{{ item.status }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  
  </div>
</template>

<script>
import { HomeIcon, MagnifyingGlassIcon, DocumentArrowDownIcon } from '@heroicons/vue/24/solid';
import $ from 'jquery';

export default {
  name: 'AccountManage',
  components: {
    HomeIcon,
    MagnifyingGlassIcon,
    DocumentArrowDownIcon,
  },
  data() {
    return {
      activeIndex: 1, // null 表示全部收起
      userAccount: '',
      username: '',
      group: '99',
      status: '99',
      statusList: [
        { id: 1, text: '全部', value: '99' },
        { id: 2, text: '啟用', value: '1' },
        { id: 3, text: '禁用', value: '0' },
      ],
      groupList: [
        { id: 1, text: '全部', value: '99' },
        { id: 2, text: 'Admin', value: '1' },
        { id: 3, text: 'User', value: '2' },
        { id: 4, text: 'Guest', value: '3' },
      ],
      items: [
        { id: 1, userAccount: 'user01', username: 'John Doe', group: 'Admin', status: '啟用' },
        { id: 2, userAccount: 'user02', username: 'Jane Smith', group: 'User', status: '禁用' },
        { id: 3, userAccount: 'user03', username: 'Paul Brown', group: 'Guest', status: '啟用' },
        { id: 4, userAccount: 'user04', username: 'Mary Johnson', group: 'Admin', status: '禁用' },
        { id: 5, userAccount: 'user05', username: 'James Wilson', group: 'User', status: '啟用' },
        { id: 6, userAccount: 'user06', username: 'Patricia Garcia', group: 'Guest', status: '禁用' },
        { id: 7, userAccount: 'user07', username: 'Robert Martinez', group: 'Admin', status: '啟用' },
        { id: 8, userAccount: 'user08', username: 'Linda Rodriguez', group: 'User', status: '禁用' },
        { id: 9, userAccount: 'user09', username: 'Michael Hernandez', group: 'Guest', status: '啟用' },
        { id: 10, userAccount: 'user10', username: 'Barbara Moore', group: 'Admin', status: '禁用' },
        { id: 11, userAccount: 'user11', username: 'William Jackson', group: 'User', status: '啟用' },
        { id: 12, userAccount: 'user12', username: 'Elizabeth Clark', group: 'Guest', status: '禁用' }
      ],
      filteredItems: [],
      table: null,
    }
  },
  methods: {
    toggleAccordion(index) {
      this.activeIndex = this.activeIndex === index ? null : index;
    },
    initializeDataTable() {
      this.table = $('#accountDatatable').DataTable({
        data: this.items,
        language: {
          lengthMenu: '顯示 _MENU_ 筆',
          info: '顯示第 _START_ 到 _END_ 筆，共 _TOTAL_ 筆資料',
          infoFiltered: '(從 _MAX_ 條中過濾)',
          infoEmpty: '顯示第 0 到 0 筆，共 0 筆資料', // 可以自定義空數據時的提示
          zeroRecords: '查無資料',
          emptyTable: '查無資料',
          search: '查詢 : ',
        },
        columns: [
          { data: 'userAccount', title: '帳號' },
          { data: 'username', title: '姓名' },
          { data: 'group', title: '群組' },
          { data: 'status', title: '狀態', 
            render: function (data) {
              let className = '';
              
              if (data == '啟用') {
                className = 'bg-cyan-50 text-cyan-600 border-cyan-200';
              } else if (data == '禁用') {
                className = 'bg-orange-50 text-orange-600 border-orange-200';
              }
              
              return `<span class="px-2 py-1 border rounded ${className}">${data}</span>`;
            }
        },
        ]
      });
    },
    applyFilter() {
      const filters = {
        userAccount: this.userAccount.trim().toLowerCase(),
        username: this.username.trim().toLowerCase(),
        group: this.groupList.find(item => item.value == this.group),
        status: this.statusList.find(item => item.value == this.status) // 如果 status 是 '99'，則不進行狀態過濾
      };
      
      if (!filters.userAccount && !filters.username && filters.group.value === '99' && filters.status.value === '99') {
        this.filteredItems = this.items;
      } else {
        this.filteredItems = this.items.filter(item => {
          console.log(!filters.userAccount || item.userAccount.toLowerCase().includes(filters.userAccount));
        // 過濾條件：使用者帳號包含使用者輸入的文字、使用者名稱包含使用者輸入的文字、群組符合、狀態符合
        
          return (!filters.userAccount || item.userAccount.toLowerCase().includes(filters.userAccount)) &&
            (!filters.username || item.username.toLowerCase().includes(filters.username)) &&
            (filters.group.value === '99' || item.group.toLowerCase() === filters.group.text.toLowerCase()) &&
            (filters.status.value === '99' || item.status.toLowerCase() === filters.status.text.toLowerCase());
        });
      }
      
      // // 獲取篩選條件，例如選中的啟用狀態
      // if (this.status == 99) {
      //   this.filteredItems = this.items;
      // } else {
      //   const statusFilter = this.statusList.find(item => item.value == this.status);
      //   // 過濾資料
      //   this.filteredItems = this.items.filter(item => item.status === statusFilter.text);
      // }

      // 重新載入 DataTables 並更新資料
      this.table.clear().rows.add(this.filteredItems).draw();
    },
  },
  mounted() {
    this.initializeDataTable();
  }
}
</script>

