<template>
  <nav class="px-20 fixed w-full flex justify-between items-center bg-white shadow-sm z-50">
    <a href="/"><img alt="Vue logo" src="../assets/logo.png" class="logo-img mx-2"></a>

    <div>
      <button 
        class="py-2 px-4 relative font-bold rounded-full hover:bg-cyan-600 hover:text-white"  ref="dropdownButton"
        @click="isOpen = !isOpen"
        v-bind:class="{ 'text-white bg-cyan-600': isOpen, 'text-gray-800': !isOpen }">
        後台管理系統
        <ChevronDownIcon class="ml-2 inline h-3 w-3" />
      </button>

      <!-- 下拉選單 -->
      <div v-if="isOpen" class="mt-2 absolute w-40 bg-white border rounded-lg shadow-lg" ref="dropdown">
        <a href="#" class="py-2 px-4 my-2 block text-gray-800 text-left hover:bg-cyan-600 hover:text-white"
        @click="handleDropdownItemClick">登入</a>
      </div>
    </div>
  </nav>
</template>

<script>
import { ChevronDownIcon } from '@heroicons/vue/24/solid';

export default {
  name: 'NavBar',
  components: {
    ChevronDownIcon
  },
  data() {
    return {
      isOpen: false,
    }
  },
  methods: {
    handleDropdownItemClick() {
      // 點選下拉選項後，收起下拉選單
      this.isOpen = false;
    },
    closeDropdownOnOutsideClick(event) {
      // 點擊網頁其他地方，收起下拉選單
      const dropdown = this.$refs.dropdown;
      const dropdownButton = this.$refs.dropdownButton;

      if (this.isOpen && !dropdown.contains(event.target) && !dropdownButton.contains(event.target)) {
        this.isOpen = false;
      }
    },
  },
  mounted() {
    document.addEventListener('click', this.closeDropdownOnOutsideClick);
  },
  beforeUnmount() {
    document.removeEventListener('click', this.closeDropdownOnOutsideClick);
  }
}
</script>

