import { createRouter, createWebHistory  } from 'vue-router';
// import { createRouter, createWebHashHistory  } from 'vue-router';
// import Router from 'vue-router';
import AuthManage from '../components/AuthManage.vue';
import AccountManage from '../components/AccountManage.vue';
import ListManage from '../components/ListManage.vue';

const router = createRouter({
  history: createWebHistory(),
  // history: createWebHashHistory(),
  routes: [
    {
      path: '/account',
      component: AccountManage,
    },
    {
      path: '/list',
      component: ListManage,
    },
    {
      path: '/auth',
      component: AuthManage,
    },
    {
      path: '/',
      redirect: '/account'
    },
    {
      path: '/index',
      redirect: '/account'
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (from.path) {
    localStorage.setItem('previousPath', from.path);
  }
  
  // 更新 web title 文字
  const routeName = localStorage.getItem('routeName');
  document.title = routeName ? `${routeName} | 全民養殖平台` : '全民養殖平台';
  next();
});

export default router;
